import { getFiles, setupPrecaching, setupRouting } from 'preact-cli/sw';

import { BackgroundSyncPlugin } from 'workbox-background-sync';
import { registerRoute } from 'workbox-routing';
import { NetworkOnly, CacheFirst, NetworkFirst, StaleWhileRevalidate } from 'workbox-strategies';
import { ExpirationPlugin } from 'workbox-expiration';

const bgSyncPlugin = new BackgroundSyncPlugin('apiRequests', {
    maxRetentionTime: 60  // retry for up to one hour (in minutes)
});

// retry failed POST requests to /api/*
registerRoute(
    // /\/api\/.*\/.*\.json/,
    /\/api\/.*/,
    new NetworkOnly({
        plugins: [bgSyncPlugin]
    }),
    'POST',
);

// retry failed PATCH requests to /api/*
registerRoute(
    // /\/api\/.*\/.*\.json/,
    /\/api\/.*/,
    new NetworkOnly({
        plugins: [bgSyncPlugin]
    }),
    'PATCH',
);

registerRoute(
    ({url}) => url.origin === 'https://themusher.litt.no' && url.pathname.startsWith('/api/users/notifications'),
    new NetworkFirst({
        cacheName: 'appapinotifications',
        plugins: [
            new ExpirationPlugin({
                maxEntries: 50,
                maxAgeSeconds: 30 * 24 * 60 * 60, // 30 Days
            }),
        ],
    }),
    'GET',
);
registerRoute(
    ({url}) => url.origin === 'https://themusher.litt.no' && url.pathname.startsWith('/api/garmin/'),
    new NetworkFirst({
        cacheName: 'appapigarmin',
        plugins: [
            new ExpirationPlugin({
                maxEntries: 50,
                maxAgeSeconds: 30 * 24 * 60 * 60, // 30 Days
            }),
        ],
    }),
    'GET',
);
registerRoute(
    ({url}) => url.origin === 'https://themusher.litt.no' && url.pathname.startsWith('/api/strava/'),
    new NetworkFirst({
        cacheName: 'appapistrava',
        plugins: [
            new ExpirationPlugin({
                maxEntries: 50,
                maxAgeSeconds: 30 * 24 * 60 * 60, // 30 Days
            }),
        ],
    }),
    'GET',
);
registerRoute(
    ({url}) => url.origin === 'https://themusher.litt.no' && url.pathname.startsWith('/api/workouts/'),
    new NetworkFirst({
        cacheName: 'appapiworkouts',
        plugins: [
            new ExpirationPlugin({
                maxEntries: 50,
                maxAgeSeconds: 30 * 24 * 60 * 60, // 30 Days
            }),
        ],
    }),
    'GET',
);

registerRoute(
    ({url}) => url.origin === 'https://themusher.litt.no' && url.pathname.startsWith('/api/'),
    new StaleWhileRevalidate({
        cacheName: 'appapi',
        plugins: [
            new ExpirationPlugin({
                maxEntries: 50,
                maxAgeSeconds: 30 * 24 * 60 * 60, // 30 Days
            }),
        ],
    }),
    'GET',
);

