registerRoute(
    ({request}) => request.destination === 'image',
        new CacheFirst({
            cacheName: 'images',
            plugins: [
                new ExpirationPlugin({
                    maxEntries: 60,
                    maxAgeSeconds: 30 * 24 * 60 * 60, // 30 Days
                }),
            ],
        })
);

setupRouting();

const urlsToCache = getFiles();
